import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {SessionService} from '../../../providers/session.service';
import {StudentService} from '../../../providers/student.service';
import {TeacherService} from '../../../providers/teacher.service';
import {SubjectService} from '../../../providers/subject.service';
import {ControlFormService} from '../../../providers/control-form.service';
import {UniverDateService} from '../../../providers/univer-date.service';

@Component({
  selector: 'app-session-edit',
  templateUrl: './session-edit.component.html',
  styleUrls: ['./session-edit.component.scss']
})
export class SessionEditComponent implements OnInit {
  public teacherData;
  public subjectData;
  public studentData;
  public controlData;
  public data;
  public periodData;

  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    student: '',
    subject: '',
    teacher: '',
    mark: '',
    controllForm: '',
    univerDate: ''
  };
  public curentData = {
    id: '',
    student: '',
    subject: '',
    teacher: '',
    mark: '',
    controllForm: '',
    univerDate: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: SessionService,
              private studentProvider: StudentService,
              private periodProvider: UniverDateService,

              private providerTeacher: TeacherService,
              private subjectProvider: SubjectService,
              private controlProvider: ControlFormService,
              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    student: [],
    subject: [],
    teacher: [],
    mark: [],
    controllForm: [],
    univerDate: []
  });
  ngOnInit() {
    this.getOne(this.idS);
    this.getTeachers();
    this.getStudents();
    this.getPeriods();
    this.getSubject();
    this.getControl();
  }
  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectPeriod(data) {
    this.univerDate.setValue(data.id);
  }
  getControl() {
    this.controlProvider.getAllData()
      .subscribe(
        res => {
          this.controlData = res;
        },
        err => console.log(err)
      );
  }
  userSelectControl(data) {
    this.controllForm.setValue(data.id);
  }
  getStudents() {
    this.studentProvider.getAllStudents()
      .subscribe(
        res => {
          this.studentData = res;
        },
        err => console.log(err)
      );
  }
  getSubject() {
    this.subjectProvider.getAllData()
      .subscribe(
        res => {
          this.subjectData = res;
        },
        err => console.log(err)
      );
  }
  userSelectStudent(data) {
    this.student.setValue(data.id);
  }
  userSelectSubject(data) {
    this.subject.setValue(data.id);
  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.student.setValue(this.curentData.student);
    this.subject.setValue(this.curentData.subject);
    this.teacher.setValue(this.curentData.teacher);
    this.mark.setValue(this.curentData.mark);
    this.controllForm.setValue(this.curentData.controllForm);
    this.univerDate.setValue(this.curentData.univerDate);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get student() {
    return this.editForm.get('student');
  }
  set student(student) {
    this.editForm.controls['student'].setValue(student);
  }
  get subject() {
    return this.editForm.get('subject');
  }
  set subject(subject) {
    this.editForm.controls['subject'].setValue(subject);
  }
  get teacher() {
    return this.editForm.get('teacher');
  }
  set teacher(teacher) {
    this.editForm.controls['teacher'].setValue(teacher);
  }
  get mark() {
    return this.editForm.get('mark');
  }
  set mark(mark) {
    this.editForm.controls['mark'].setValue(mark);
  }

  get controllForm() {
    return this.editForm.get('controllForm');
  }
  set controllForm(controllForm) {
    this.editForm.controls['controllForm'].setValue(controllForm);
  }
  get univerDate() {
    return this.editForm.get('univerDate');
  }
  set univerDate(univerDate) {
    this.editForm.controls['univerDate'].setValue(univerDate);
  }
  sendNewData() {
    this.newData.univerDate = this.univerDate.value;
    this.newData.student = this.student.value;
    this.newData.controllForm = this.controllForm.value;
    this.newData.mark = this.mark.value;
    this.newData.teacher = this.teacher.value;
    this.newData.id = this.id.value;
    this.newData.subject = this.subject.value;
    this.provider.editData(this.newData);
  }

}
