import { Component, OnInit } from '@angular/core';
import {StudentService} from '../providers/student.service';
import {ActivatedRoute, Router} from '@angular/router';
import {GroupsService} from '../providers/groups.service';

@Component({
  selector: 'app-student-list',
  templateUrl: './student-list.component.html',
  styleUrls: ['./student-list.component.scss']
})
export class StudentListComponent implements OnInit {
  public students = [];
  public searchStudents = [];
  public grups;
public updateDate;
  constructor(
    private _actRouter: ActivatedRoute,
    private _studentService: StudentService,
    private _router: Router,
    private grupsService: GroupsService
  ) { }
  ngOnInit() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getStudents();
    this.getGrups();
    }
    updateInfo() {
      this.getStudents();
      this.getGrups();
      this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    }
  getStudents() {
    let search = this._actRouter.snapshot.paramMap.get('founded');
    if (search == 'founded') {
    } else {
      this._studentService.getAllStudents()
        .subscribe(
          res => {
            this.students = res;
          },
          err => console.log(err)
        );
    }
  }
  goToMakeRequest() {
    this._router.navigate(['student/make-request']);

  }
  goToNewStudentComponent() {
    this._router.navigate(['/student/new', this.getIdForNewStudent() + 1]);
  }
  sortById() {
    let temp;
    for (let key = 0; key < this.students.length; key++) {
      for (let key2 = 0; key2 < this.students.length; key2++) {
        if ( this.students[key].id < this.students[key2].id) {
          temp = this.students[key];
          this.students[key] = this.students[key2];
          this.students[key2] = temp;
        }
      }
    }
    }
  sortByGroup() {
    let temp;
    for (let key = 0; key < this.students.length; key++) {
      for (let key2 = 0; key2 < this.students.length; key2++) {
        if ( this.students[key].group < this.students[key2].group) {
          temp = this.students[key];
          this.students[key] = this.students[key2];
          this.students[key2] = temp;
        }
      }
    }
  }
  sortByStypendia() {
    let temp;
    for (let key = 0; key < this.students.length; key++) {
      for (let key2 = 0; key2 < this.students.length; key2++) {
        if ( this.students[key].stypendia < this.students[key2].stypendia) {
          temp = this.students[key];
          this.students[key] = this.students[key2];
          this.students[key2] = temp;
        }
      }
    }
  }
  getIdForNewStudent() {
    let arr = [];
    for (let key = 0; key < this.students.length; key++) {
      arr[key] = this.students[key].id;
    }
    let max = Math.max(...arr);
    if (arr.length == 0) {
      return 0;
    } else {
      return max;
    }
  }

  delete(student) {
    this._studentService.deleteStudent(student.id);
    let h = this;
    setTimeout(function () {
      h.updateInfo();
    }, 40);
  }
  goEdit(student) {
    this._router.navigate(['/students/edit/', student.id]);
  }
  GrupaFromIdToName(grupaID) {
    for (let gr in this.grups) {
      if (this.grups[gr].id == grupaID) {
        return this.grups[gr].name;
      }
    }
  }
  getGrups() {
    this.grupsService.getAllData()
      .subscribe(
        res => {
          this.grups = res;
        },
        err => console.log(err)
      );
  }

}
