import { Component, OnInit } from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {TeacherService} from '../../../providers/teacher.service';
import {KategoryTeacherService} from '../../../providers/kategory-teacher.service';
import {KafedraService} from '../../../providers/kafedra.service';

@Component({
  selector: 'app-query2',
  templateUrl: './query2.component.html',
  styleUrls: ['./query2.component.scss']
})
export class Query2Component implements OnInit {

  public maping = this._service.routes;
  public modalData;
  public kafedraData;

  public requestData = {
    kafedra: '',
    category: '',
    year: '',
    age: '',
    children: '',
    salary: '',
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private _service: TeacherService,
              private _actRouter: ActivatedRoute,
              private providerKafedra: KafedraService,
              private providerCategory: KategoryTeacherService
  ) { }
  requestForm = this.fb.group({
    kafedra: [''],
    category: [''],
    year: [''],
    age: [''],
    children: [''],
    salary: [''],
  });
  ngOnInit() {
    this.getKafedra();
    this.getKategory();
  }
  getKafedra() {
    this.providerKafedra.getAllData()
      .subscribe(
        res => {
          this.kafedraData = res;
        },
        err => console.log(err)
      );
  }
  getKategory() {
    this.providerCategory.getAllData()
      .subscribe(
        res => {
          this.modalData = res;
        },
        err => console.log(err)
      );
  }
  get kafedra() {
    return this.requestForm.get('kafedra');
  }
  get category() {
    return this.requestForm.get('category');
  }
  get children() {
    return this.requestForm.get('children');
  }
  get year() {
    return this.requestForm.get('year');
  }
  get age() {
    return this.requestForm.get('age');
  }
  get salary() {
    return this.requestForm.get('salary');
  }
  set category(category) {
    this.requestForm.controls['category'].setValue(category);
  }
  set kafedra(kafedra) {
    this.requestForm.controls['kafedra'].setValue(kafedra);
  }
  sendData() {
    this.requestData.category = this.category.value;
    this.requestData.kafedra = this.kafedra.value;
    this.requestData.age = this.age.value;
    this.requestData.salary = this.salary.value;
    this.requestData.year = this.year.value;
    this.requestData.children = this.children.value;
    console.log(this.requestData)
    this._service.makeRequest(this.requestData);
  }
  userSelectKategory(data) {
    console.log(data)
    this.category.setValue(data.name);
  }
  userSelectKafedra(data) {
    this.kafedra.setValue(data.id);

  }
}
