import { Component, OnInit } from '@angular/core';
import {AuthService} from '../providers/auth.service';
import {Router} from '@angular/router';
import {FormBuilder, Validators} from '@angular/forms';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../environments/environment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  serverSrc = environment.serverSrc + '/mylogin';

  loginUserData = {
    password: '',
    userName: ''
  };
  constructor(private _auth: AuthService, private _router: Router, private fb: FormBuilder, private http: HttpClient) {
  }
  loginForm = this.fb.group({
    email: ['', [
      Validators.required,
    ]
    ],
    password: ['', [
      Validators.required,
    ]
    ]
  });
  ngOnInit() {
  }
  get email() {
    return this.loginForm.get('email');
  }
  get password() {
    return this.loginForm.get('password');
  }
  loginUser() {
    this.loginUserData.userName = this.email.value;
    this.loginUserData.password = this.password.value;
    this.http.post<Observable<boolean>>(this.serverSrc, {
      userName: this.email.value,
      password: this.password.value
    }).subscribe(isValid => {
      if (isValid) {
        sessionStorage.setItem(
          'token',
          btoa(this.loginUserData.userName + ':' + this.loginUserData.password)
        );
        this._router.navigate(['/students']);
      } else {
        alert('Authentication failed.');
      }
    });
  }
}
