import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {StudentService} from '../../providers/student.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-student-detail',
  templateUrl: './student-detail.component.html',
  styleUrls: ['./student-detail.component.css']
})
export class StudentDetailComponent implements OnInit {
public studentId;
public student;
public grups;
  constructor(private _actRouter: ActivatedRoute, private _studentService: StudentService, private _router: Router) { }

  ngOnInit() {
    let id = this._actRouter.snapshot.paramMap.get('id');
    // this._studentService.getOneStudent(id)
    //   .subscribe(
    //     res => this.student = res,
    //     err => console.log(err)
    //   );
    this.student;
    this.studentId = id;
    this.grups = JSON.parse(localStorage.getItem('grups'));
    this.student.grupaId = this.selectedGrupa(this.student.grupaId);
  }
  selectedGrupa(grupaID) {
    for (let gr in this.grups) {
      if (this.grups[gr].id == grupaID) {
        return this.grups[gr].name;
      }
    }
  }

  goEdit(student) {
    this._router.navigate(['/students/edit/', student.id]);
  }
}
