import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {TeacherService} from '../../../providers/teacher.service';
import {KategoryTeacherService} from '../../../providers/kategory-teacher.service';
import {KafedraService} from '../../../providers/kafedra.service';
import {SpecialityTeachersService} from '../../../providers/speciality-teachers.service';

@Component({
  selector: 'app-teacher-create',
  templateUrl: './teacher-create.component.html',
  styleUrls: ['./teacher-create.component.scss']
})
export class TeacherCreateComponent implements OnInit {

  public newData = {
    id: '',
    name: '',
    lastName: '',
    afterFathName: '',
    specialityTeacher: '',
    kategoryT: '',
    countChildren: '',
    salary: '',
    kafedra: ''
  };
  public modalData;
  public kafedraData;
  public specialityDate;

  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: TeacherService,
              private providerCategory: KategoryTeacherService,
              private providerKafedra: KafedraService,
              private providerSpeciality: SpecialityTeachersService,

              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    name: [''],
    lastName: [''],
    afterFathName: [''],
    specialityTeacher: [''],
    kategoryT: [''],
    countChildren: [''],
    salary: [''],
    kafedra: ['']
  });
  ngOnInit() {
    this.getSpeciality()

    this.getKategory();
    this.getKafedra();
  }
  getSpeciality() {
    this.providerSpeciality.getAllData()
      .subscribe(
        res => {
          this.specialityDate = res;
        },
        err => console.log(err)
      );
  }
  userSelectspeciality(data) {
    this.specialityTeacher.setValue(data.name);

  }
  get id() {
    return this.newForm.get('id');
  }
  set id(id) {
    this.newForm.controls['id'].setValue(id);
  }
  get name() {
    return this.newForm.get('name');
  }
  set name(name) {
    this.newForm.controls['name'].setValue(name);
  }
  get lastName() {
    return this.newForm.get('lastName');
  }
  set lastName(lastName) {
    this.newForm.controls['lastName'].setValue(lastName);
  }
  get afterFathName() {
    return this.newForm.get('afterFathName');
  }
  set afterFathName(afterFathName) {
    this.newForm.controls['afterFathName'].setValue(afterFathName);
  }
  get specialityTeacher() {
    return this.newForm.get('specialityTeacher');
  }
  set specialityTeacher(specialityTeacher) {
    this.newForm.controls['specialityTeacher'].setValue(specialityTeacher);
  }
  get kategoryT() {
    return this.newForm.get('kategoryT');
  }
  set kategoryT(kategoryT) {
    this.newForm.controls['kategoryT'].setValue(kategoryT);
  }
  get countChildren() {
    return this.newForm.get('countChildren');
  }
  set countChildren(countChildren) {
    this.newForm.controls['countChildren'].setValue(countChildren);
  }
  get salary() {
    return this.newForm.get('salary');
  }
  set salary(salary) {
    this.newForm.controls['salary'].setValue(salary);
  }
  get kafedra() {
    return this.newForm.get('kafedra');
  }
  set kafedra(kafedra) {
    this.newForm.controls['kafedra'].setValue(kafedra);
  }
  getKategory() {
    this.providerCategory.getAllData()
      .subscribe(
        res => {
          this.modalData = res;
        },
        err => console.log(err)
      );
  }
  getKafedra() {
    this.providerKafedra.getAllData()
      .subscribe(
        res => {
          this.kafedraData = res;
        },
        err => console.log(err)
      );
  }
  sendData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.newData.lastName = this.lastName.value;
    this.newData.afterFathName = this.afterFathName.value;
    this.newData.specialityTeacher = this.specialityTeacher.value;
    this.newData.kategoryT = this.kategoryT.value;
    this.newData.countChildren = this.countChildren.value;
    this.newData.salary = this.salary.value;
    this.newData.kafedra = this.kafedra.value;
    this.provider.newData(this.newData);
  }
  openModalWindow () {
  }
  userSelectKategory(data) {
    this.kategoryT.setValue(data.name);
  }
  userSelectKafedra(data) {
    this.kafedra.setValue(data.id);

  }
}
