import { Component, OnInit } from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {KafedraService} from '../../../providers/kafedra.service';

@Component({
  selector: 'app-query3',
  templateUrl: './query3.component.html',
  styleUrls: ['./query3.component.scss']
})
export class Query3Component implements OnInit {

  public kafedraData;

  public requestData = {
    kafedra: ''
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private queryProvider: QueryProviderService,
              private providerKafedra: KafedraService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    kafedra: [''],
  });
  ngOnInit() {
    this.getKafedra();
  }
  getKafedra() {
    this.providerKafedra.getAllData()
      .subscribe(
        res => {
          this.kafedraData = res;
        },
        err => console.log(err)
      );
  }
  userSelectKafedra(data) {
    this.kafedra.setValue(data.id);

  }
  set kafedra(kafedra) {
    this.requestForm.controls['kafedra'].setValue(kafedra);
  }
  get kafedra() {
    return this.requestForm.get('kafedra');
  }
  sendRequest() {
    this.requestData.kafedra = this.kafedra.value;
    this.queryProvider.makeQuery3(this.requestData);
  }

}
