import { Component, OnInit } from '@angular/core';
import {KategoryTeacherService} from '../../providers/kategory-teacher.service';
import {ActivatedRoute, Router} from '@angular/router';
import {TeacherService} from '../../providers/teacher.service';
import {KafedraService} from '../../providers/kafedra.service';
import {FormBuilder} from '@angular/forms';
import {TeachersService} from '../../querry/querry-service/teachers/teachers.service';
import {CandidateWorkService} from '../../providers/candidate-work.service';

@Component({
  selector: 'app-candidate-work-edit',
  templateUrl: './candidate-work-edit.component.html',
  styleUrls: ['./candidate-work-edit.component.scss']
})
export class CandidateWorkEditComponent implements OnInit {
  public data;
  public updateDate;
  public modalData;
  public routes = this.provider.routes;
  public kafedraData;

  public newData = {
    id: '',
    themeOfWork: '',
    teacher: '',
    date: '',
  };
  public teacherData;
  public curentData = {
    id: this._actRouter.snapshot.paramMap.get('id'),
    themeOfWork: '',
    teacher: '',
    date: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: CandidateWorkService,
              private _actRouter: ActivatedRoute,
              private providerTeacher: TeacherService
  ) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    themeOfWork: [],
    teacher: [],
    date: [],
  });
  ngOnInit() {
    this.getOne(this.idS);
    this.getTeachers();

  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneCandidateWork(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.themeOfWork.setValue(this.curentData.themeOfWork);
    this.teacher.setValue(this.curentData.teacher);
    this.date.setValue(this.curentData.date);
  }
  get id() {
    return this.editForm.get('id');
  }
  get teacher() {
    return this.editForm.get('teacher');
  }
  get date() {
    return this.editForm.get('date');
  }
  get themeOfWork() {
    return this.editForm.get('themeOfWork');
  }
  set teacher(teacher) {
    this.editForm.controls['teacher'].setValue(teacher);
  }
  set themeOfWork(themeOfWork) {
    this.editForm.controls['themeOfWork'].setValue(themeOfWork);
  }
  set date(date) {
    this.editForm.controls['date'].setValue(date);
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  sendNewData() {
    this.newData.date = this.date.value;
    this.newData.id = this.id.value;
    this.newData.themeOfWork = this.themeOfWork.value;
    this.newData.teacher = this.teacher.value;
    this.provider.editCandidateWork(this.newData);
  }

}
