import { Component, OnInit } from '@angular/core';
import {QueryProviderService} from '../../providers/query-provider.service';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-query',
  templateUrl: './query.component.html',
  styleUrls: ['./query.component.scss']
})
export class QueryComponent implements OnInit {
  search = this._actRouter.snapshot.paramMap.get('id');
  public answData;
  keys = [];
  keysOrigin = [];
  dictionary = {
    'theme': 'Тема',
    'teacher': 'Викладач',
    'name': 'Назва',
    'id': 'Id',
    'grupa': 'Група',
    'lastName': 'Прізвище',
    'afterFathName': 'По-батькові',
    'afterFName': 'По-батькові',
    'dateOfBirth': 'Дата народження',
    'specialityTeacher': 'Спеціальність',
    'kategoryT': 'Категорія',
    'nameTeacher': 'Ім"я',
    'lastNameTeacher': 'Прізвище',
    'themeDiplom': 'Тема дипломної',
    'occupation': 'Предмет',
    'hours': 'Кількість годин',
    'countChildren': 'К-сть дітей',
    'countOfChildren': 'К-сть дітей',
    'salary': 'Зарплата',
    'kafedra': 'Кафедра',
    'stypendia': 'Стипендія',
  };
  constructor(
    private queryProvider: QueryProviderService,
    private _actRouter: ActivatedRoute
  ) { }

  ngOnInit() {
    this.getResult();
  }
  getResult() {
    this.queryProvider.getResultQuery(this.search)
      .subscribe(
        res => {
          this.answData = res;
          this.getKeys();
        },
        err => console.log(err)
      );
  }
  getKeys() {
    console.log(this.answData)
    for (let key in this.answData[0]) {
      this.keys.push(this.dictionary[key]);
      this.keysOrigin.push(key);
    }
  }
}
