import { Injectable } from '@angular/core';
import {Router} from '@angular/router';
import {ApiService} from './api.service';
import {environment} from '../../environments/environment';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class KindLessonsService {

  public serverSrc = environment.serverSrc;
  public routes = {
    'main': '/kind-lessons',
    'new': '/kind-lessons/new',
    'edit': '/kind-lessons/edit'
  };
  public maping = this._maping.kind_lessons_api;
  constructor(private http: HttpClient, private _router: Router, private _maping: ApiService) { }
  getAllData() {
    return this.http.get<any>(this.serverSrc + this.maping['showAll']);
  }
  getOneData(id) {
    return this.http.get<any>(this.serverSrc + this.maping['showOne'] + '/' + id);
  }
  editData(group) {
    this.http.post(this.serverSrc + this.maping['edit'], group)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  redirectToMainPage() {
    this._router.navigate([this.routes.main]);
  }
  newData(work) {
    return this.http.post(this.serverSrc + this.maping['create'], work)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  deleteData(id) {
    return this.http.post(this.serverSrc + this.maping['delete'], id)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
}
