import { Component, OnInit } from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {SubjectService} from '../../../providers/subject.service';
import {ActivatedRoute, Router} from '@angular/router';
import {GroupsService} from '../../../providers/groups.service';
import {TeacherService} from '../../../providers/teacher.service';

@Component({
  selector: 'app-query11',
  templateUrl: './query11.component.html',
  styleUrls: ['./query11.component.scss']
})
export class Query11Component implements OnInit {
  public requestData = {
    teacher: '',
    kafedra: ''
  };
  public teacherData;

  constructor(private _router: Router,
              private fb: FormBuilder,
              private providerTeacher: TeacherService,
              private queryProvider: QueryProviderService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    teacher: [''],
    kafedra: ['']
  });
  ngOnInit() {
    this.getTeachers();

  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  set kafedra(kafedra) {
    this.requestForm.controls['kafedra'].setValue(kafedra);
  }
  get kafedra() {
    return this.requestForm.get('kafedra');
  }
  set teacher(teacher) {
    this.requestForm.controls['teacher'].setValue(teacher);
  }
  get teacher() {
    return this.requestForm.get('teacher');
  }
  sendRequest() {
    this.requestData.teacher = this.teacher.value;
    this.requestData.kafedra = this.kafedra.value;
    this.queryProvider.makeQuery11(this.requestData);
  }

}
