import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {KindLessonsService} from '../../../providers/kind-lessons.service';
import {OccupationService} from '../../../providers/occupation.service';
import {StudingPlanService} from '../../../providers/studing-plan.service';
import {TeacherService} from '../../../providers/teacher.service';

@Component({
  selector: 'app-occupation-edit',
  templateUrl: './occupation-edit.component.html',
  styleUrls: ['./occupation-edit.component.scss']
})
export class OccupationEditComponent implements OnInit {

  public data;
  public studingPlanData;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    nameTeacher: '',
    idStudingPlan: '',
  };
  public curentData = {
    id: '',
    nameTeacher: '',
    idStudingPlan: '',
  };
  public teacherData;

  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: OccupationService,
              private providerStudingPlan: StudingPlanService,
              private providerTeacher: TeacherService,
  private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    nameTeacher: [],
    idStudingPlan: [],

  });
  ngOnInit() {
    this.getOne(this.idS);
    this.getStudingPlan();
    this.getTeachers();

  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  userSelectTeacher(data) {
    this.nameTeacher.setValue(data.id);
  }
  userSelectStudingPlan(data) {
    this.idStudingPlan.setValue(data.id);
  }
  getStudingPlan() {
    this.providerStudingPlan.getAllData()
      .subscribe(
        res => {
          this.studingPlanData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.nameTeacher.setValue(this.curentData.nameTeacher);
    this.idStudingPlan.setValue(this.curentData.idStudingPlan);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get nameTeacher() {
    return this.editForm.get('nameTeacher');
  }
  get idStudingPlan() {
    return this.editForm.get('idStudingPlan');
  }
  set nameTeacher(nameTeacher) {
    this.editForm.controls['nameTeacher'].setValue(nameTeacher);
  }
  set idStudingPlan(idStudingPlan) {
    this.editForm.controls['idStudingPlan'].setValue(idStudingPlan);
  }
  sendNewData() {
    this.newData.nameTeacher = this.nameTeacher.value;
    this.newData.id = this.id.value;
    this.newData.idStudingPlan = this.idStudingPlan.value;
    this.provider.editData(this.newData);
  }


}
