import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {KindLessonsService} from '../../../providers/kind-lessons.service';

@Component({
  selector: 'app-kind-lessons-create',
  templateUrl: './kind-lessons-create.component.html',
  styleUrls: ['./kind-lessons-create.component.scss']
})
export class KindLessonsCreateComponent implements OnInit {

  public newData = {
    id: '',
    name: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: KindLessonsService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    name: [''],
  });
  ngOnInit() {
  }
  get id() {
    return this.newForm.get('id');
  }
  get name() {
    return this.newForm.get('name');
  }
  sendData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.provider.newData(this.newData);
  }

}
