import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {StudentService} from '../../../providers/student.service';
import {KafedraService} from '../../../providers/kafedra.service';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {UniverDateService} from '../../../providers/univer-date.service';
import {GroupsService} from '../../../providers/groups.service';

@Component({
  selector: 'app-query1',
  templateUrl: './query1.component.html',
  styleUrls: ['./query1.component.scss']
})
export class Query1Component implements OnInit {
  public groupData;
  public periodData;
  public requestStudentData = {
    grupaName: '',
    period: ''
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private queryProvider: QueryProviderService,
              private groupProvider: GroupsService,
              private periodProvider: UniverDateService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    grupa: [''],
    period: [''],
  });
  ngOnInit() {
    this.getPeriods();
    this.getGroups();
  }
  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectPeriod(data) {
    this.period.setValue(data.id);
  }
  userSelectGroup(data) {
    this.grupa.setValue(data.name);
  }
  getGroups() {
    this.groupProvider.getAllData()
      .subscribe(
        res => {
          this.groupData = res;
        },
        err => console.log(err)
      );
  }
  set grupa(grupa) {
    this.requestForm.controls['grupa'].setValue(grupa);
  }
  get grupa() {
    return this.requestForm.get('grupa');
  }
  set period(period) {
    this.requestForm.controls['period'].setValue(period);
  }
  get period() {
    return this.requestForm.get('period');
  }
  sendRequest() {
    this.requestStudentData.grupaName = this.grupa.value;
    this.requestStudentData.period = this.period.value;
    this.queryProvider.makeQuery4(this.requestStudentData);
  }

}
