import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {FormBuilder} from '@angular/forms';
import {UniverDateService} from '../../../providers/univer-date.service';
import {TeacherService} from '../../../providers/teacher.service';

@Component({
  selector: 'app-query13',
  templateUrl: './query13.component.html',
  styleUrls: ['./query13.component.scss']
})
export class Query13Component implements OnInit {
  public periodData;

  public requestData = {
    idTeacher: '',
    idStudingYear: ''
  };
  public teacherData;

  constructor(private _router: Router,
              private fb: FormBuilder,
              private periodProvider: UniverDateService,
              private providerTeacher: TeacherService,
              private queryProvider: QueryProviderService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    teacher: [''],
    period: ['']
  });
  ngOnInit() {
    this.getPeriods();
    this.getTeachers();

  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectPeriod(data) {
    this.period.setValue(data.id);
  }
  set period(period) {
    this.requestForm.controls['period'].setValue(period);
  }
  get period() {
    return this.requestForm.get('period');
  }
  set teacher(teacher) {
    this.requestForm.controls['teacher'].setValue(teacher);
  }
  get teacher() {
    return this.requestForm.get('teacher');
  }
  sendRequest() {
    this.requestData.idTeacher = this.teacher.value;
    this.requestData.idStudingYear = this.period.value;
    this.queryProvider.makeQuery13(this.requestData);
  }

}
