import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {DocentWorkService} from '../../../providers/docent-work.service';
import {TeacherService} from '../../../providers/teacher.service';

@Component({
  selector: 'app-docent-work-create',
  templateUrl: './docent-work-create.component.html',
  styleUrls: ['./docent-work-create.component.scss']
})
export class DocentWorkCreateComponent implements OnInit {
  public teacherData;

  public newData = {
    id: '',
    teacher: '',
    themeOfWork: '',
    date: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private providerTeacher: TeacherService,
              private provider: DocentWorkService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    themeOfWork: [''],
    teacher: [''],
    date: [''],
  });
  ngOnInit() {
    this.getTeachers();

  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  get id() {
    return this.newForm.get('id');
  }
  set teacher(teacher) {
    this.newForm.controls['teacher'].setValue(teacher);
  }
  get themeOfWork() {
    return this.newForm.get('themeOfWork');
  }
  get teacher() {
    return this.newForm.get('teacher');
  }
  get date() {
    return this.newForm.get('date');
  }
  sendData() {
    this.newData.date = this.date.value;
    this.newData.id = this.id.value;
    this.newData.teacher = this.teacher.value;
    this.newData.themeOfWork = this.themeOfWork.value;
    this.provider.newData(this.newData);
  }

}
