import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {KafedraService} from '../../../providers/kafedra.service';
import {TeacherService} from '../../../providers/teacher.service';
import {FacultyService} from '../../../providers/faculty.service';

@Component({
  selector: 'app-kafedra-edit',
  templateUrl: './kafedra-edit.component.html',
  styleUrls: ['./kafedra-edit.component.scss']
})
export class KafedraEditComponent implements OnInit {
  public teacherData;
  public facultyData;

  public data;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    name: '',
    faculty: '',
    zavKafedra: ''
  };
  public curentData = {
    id: '',
    name: '',
    faculty: '',
    zavKafedra: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: KafedraService,
              private providerTeacher: TeacherService,
              private providerFaculty: FacultyService,

              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    name: [],
    zavKafedra: [],
    faculty: [],
  });
  ngOnInit() {
    this.getTeachers();

    this.getOne(this.idS);
    this.getFaculty();

  }
  userSelectFaculty(data) {
    this.faculty.setValue(data.id);
  }
  getFaculty() {
    this.providerFaculty.getAllData()
      .subscribe(
        res => {
          this.facultyData = res;
        },
        err => console.log(err)
      );
  }
  userSelectTeacher(data) {
    this.zavKafedra.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.name.setValue(this.curentData.name);
    this.faculty.setValue(this.curentData.faculty);
    this.zavKafedra.setValue(this.curentData.zavKafedra);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get name() {
    return this.editForm.get('name');
  }
  set name(name) {
    this.editForm.controls['name'].setValue(name);
  }
  get zavKafedra() {
    return this.editForm.get('zavKafedra');
  }
  set zavKafedra(zavKafedra) {
    this.editForm.controls['zavKafedra'].setValue(zavKafedra);
  }
  get faculty() {
    return this.editForm.get('faculty');
  }
  set faculty(faculty) {
    this.editForm.controls['faculty'].setValue(faculty);
  }
  sendNewData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.newData.zavKafedra = this.zavKafedra.value;
    this.newData.faculty = this.faculty.value;
    console.log(this.newData)
    this.provider.editData(this.newData);
  }
}
