import { Component, OnInit } from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {GroupsService} from '../../../providers/groups.service';
import {UniverDateService} from '../../../providers/univer-date.service';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {SubjectService} from '../../../providers/subject.service';

@Component({
  selector: 'app-query9',
  templateUrl: './query9.component.html',
  styleUrls: ['./query9.component.scss']
})
export class Query9Component implements OnInit {
  public subjectData;

  public groupData;
  public periodData;
  public requestStudentData = {
    grupa: '',
    semestr: '',
    subject: ''
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private queryProvider: QueryProviderService,
              private subjectProvider: SubjectService,

              private groupProvider: GroupsService,
              private periodProvider: UniverDateService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    grupa: [''],
    semestr: [''],
    subject: [''],
  });
  ngOnInit() {
    this.getPeriods();
    this.getGroups();
    this.getSubjects();

  }
  getSubjects() {
    this.subjectProvider.getAllData()
      .subscribe(
        res => {
          this.subjectData = res;
        },
        err => console.log(err)
      );
  }
  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectPeriod(data) {
    this.semestr.setValue(data.id);
  }
  userSelectGroup(data) {
    this.grupa.setValue(data.name);
  }
  userSelectSubject(data) {
    this.subject.setValue(data.id);
  }
  getGroups() {
    this.groupProvider.getAllData()
      .subscribe(
        res => {
          this.groupData = res;
        },
        err => console.log(err)
      );
  }
  set grupa(grupa) {
    this.requestForm.controls['grupa'].setValue(grupa);
  }
  get grupa() {
    return this.requestForm.get('grupa');
  }
  set semestr(semestr) {
    this.requestForm.controls['semestr'].setValue(semestr);
  }
  get semestr() {
    return this.requestForm.get('semestr');
  }
  set subject(subject) {
    this.requestForm.controls['subject'].setValue(subject);
  }
  get subject() {
    return this.requestForm.get('subject');
  }
  sendRequest() {
    this.requestStudentData.grupa = this.grupa.value;
    this.requestStudentData.semestr = this.semestr.value;
    this.requestStudentData.subject = this.subject.value;
    console.log(this.requestStudentData)
    this.queryProvider.makeQuery9(this.requestStudentData);
  }
}
