import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {DecanatService} from '../../providers/decanat.service';
import {TeacherService} from '../../providers/teacher.service';

@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.scss']
})
export class TeacherComponent implements OnInit {

  public routes = this.provider.routes;
  public data = [];
  public updateDate;
  constructor(
    private provider: TeacherService,
    private _router: Router,
    private _actRouter: ActivatedRoute,

) { }

  ngOnInit() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getData();
  }
  goToMakeRequest() {
    this._router.navigate(['teacher/make-request']);

  }
  updateInfo() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getData();
  }
  getData() {
    let search = this._actRouter.snapshot.paramMap.get('founded');
    if (search == 'founded') {
      this.provider.getSearchData()
        .subscribe(
          res => {
            console.log(res)
            this.data = res;
          },
          err => console.log(err)
        );
    } else {
      this.provider.getAllData()
        .subscribe(
          res => {
            this.data = res;
          },
          err => console.log(err)
        );
    }
  }
  delete(info) {
    this.provider.deleteData(info.id);
    let h = this;
    setTimeout(function () {
      h.updateInfo();
    }, 40);
  }
  goToEdit(info) {
    this._router.navigate([this.routes.edit + '/', info.id]);
  }
  goToNew() {
    this._router.navigate([this.routes.new + '/' , this.getIdForNewData() + 1]);
  }
  getIdForNewData() {
    let arr = [];
    for (let key = 0; key < this.data.length; key++) {
      arr[key] = this.data[key].id;
    }
    let max = Math.max(...arr);
    console.log(max);
    if (arr.length == 0) {
      return 0;
    } else {
      return max;
    }
  }

}
