import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {GroupsService} from '../../../providers/groups.service';
import {DiplomaService} from '../../../providers/diploma.service';
import {TeacherService} from '../../../providers/teacher.service';
import {StudentService} from '../../../providers/student.service';

@Component({
  selector: 'app-diploma-edit',
  templateUrl: './diploma-edit.component.html',
  styleUrls: ['./diploma-edit.component.scss']
})
export class DiplomaEditComponent implements OnInit {
  public teacherData;
  public studentData;

  public data;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    student: '',
    kerivnyk: '',
    theme: '',
    date: ''
  };
  public curentData = {
    id: '',
    student: '',
    kerivnyk: '',
    theme: '',
    date: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: DiplomaService,
              private studentProvider: StudentService,
              private providerTeacher: TeacherService,
              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    student: [],
    kerivnyk: [],
    theme: [],
    date: []
  });
  ngOnInit() {
    this.getOne(this.idS);
    this.getTeachers();
    this.getStudents();
  }
  getStudents() {
    this.studentProvider.getAllStudents()
      .subscribe(
        res => {
          this.studentData = res;
        },
        err => console.log(err)
      );
  }
  userSelectStudent(data) {
    this.student.setValue(data.id);
  }
  userSelectTeacher(data) {
    this.kerivnyk.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.student.setValue(this.curentData.student);
    this.kerivnyk.setValue(this.curentData.kerivnyk);
    this.theme.setValue(this.curentData.theme);
    this.date.setValue(this.curentData.date);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get student() {
    return this.editForm.get('student');
  }
  set student(student) {
    this.editForm.controls['student'].setValue(student);
  }
  get kerivnyk() {
    return this.editForm.get('kerivnyk');
  }
  set kerivnyk(kerivnyk) {
    this.editForm.controls['kerivnyk'].setValue(kerivnyk);
  }
  get theme() {
    return this.editForm.get('theme');
  }
  set theme(theme) {
    this.editForm.controls['theme'].setValue(theme);
  }
  get date() {
    return this.editForm.get('date');
  }
  set date(date) {
    this.editForm.controls['date'].setValue(date);
  }
  sendNewData() {
    this.newData.student = this.student.value;
    this.newData.id = this.id.value;
    this.newData.kerivnyk = this.kerivnyk.value;
    this.newData.theme = this.theme.value;
    this.newData.date = this.date.value;
    this.provider.editData(this.newData);
  }

}
