import { Component, OnInit } from '@angular/core';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {KafedraService} from '../../../providers/kafedra.service';
import {SubjectService} from '../../../providers/subject.service';
import {GroupsService} from '../../../providers/groups.service';

@Component({
  selector: 'app-query5',
  templateUrl: './query5.component.html',
  styleUrls: ['./query5.component.scss']
})
export class Query5Component implements OnInit {


  public grupaData;
  public subjectData;

  public requestData = {
    subjectId: '',
    grupaName: ''
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private queryProvider: QueryProviderService,
              private subjectProvider: SubjectService,
              private grupaProvider: GroupsService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    subjectId: [''],
    grupaName: ['']
  });
  ngOnInit() {
    this.getGrups();
    this.getSubjects();
  }
  getGrups() {
    this.grupaProvider.getAllData()
      .subscribe(
        res => {
          this.grupaData = res;
        },
        err => console.log(err)
      );
  }
  getSubjects() {
    this.subjectProvider.getAllData()
      .subscribe(
        res => {
          this.subjectData = res;
        },
        err => console.log(err)
      );
  }
  userSelectGrupa(data) {
    this.grupaName.setValue(data.name);
  }
  userSelectSubject(data) {
    this.subjectId.setValue(data.id);
  }
  set subjectId(subjectId) {
    this.requestForm.controls['subjectId'].setValue(subjectId);
  }
  get subjectId() {
    return this.requestForm.get('subjectId');
  }
  set grupaName(grupaName) {
    this.requestForm.controls['grupaName'].setValue(grupaName);
  }
  get grupaName() {
    return this.requestForm.get('grupaName');
  }
  sendRequest() {
    this.requestData.grupaName = this.grupaName.value;
    this.requestData.subjectId = this.subjectId.value;
    this.queryProvider.makeQuery5(this.requestData);
  }

}
