import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {DecanatService} from '../../providers/decanat.service';
import {UniverDateService} from '../../providers/univer-date.service';

@Component({
  selector: 'app-univer-date',
  templateUrl: './univer-date.component.html',
  styleUrls: ['./univer-date.component.scss']
})
export class UniverDateComponent implements OnInit {

  public routes = this.provider.routes;
  public data = [];
  public updateDate;
  constructor(private provider: UniverDateService, private _router: Router) { }

  ngOnInit() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getData();
  }
  updateInfo() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getData();
  }
  getData() {
    this.provider.getAllData()
      .subscribe(
        res => {
          this.data = res;
        },
        err => console.log(err)
      );
  }
  delete(info) {
    this.provider.deleteData(info.id);
    let h = this;
    setTimeout(function () {
      h.updateInfo();
    }, 40);
  }
  goToEdit(info) {
    this._router.navigate([this.routes.edit + '/', info.id]);
  }
  goToNew() {
    this._router.navigate([this.routes.new + '/' , this.getIdForNewData() + 1]);
  }
  getIdForNewData() {
    let arr = [];
    for (let key = 0; key < this.data.length; key++) {
      arr[key] = this.data[key].id;
    }
    let max = Math.max(...arr);
    console.log(max);
    if (arr.length == 0) {
      return 0;
    } else {
      return max;
    }
  }

}
