import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {StudingPlanService} from '../../../providers/studing-plan.service';
import {ControlFormService} from '../../../providers/control-form.service';
import {GroupsService} from '../../../providers/groups.service';
import {UniverDateService} from '../../../providers/univer-date.service';
import {SubjectService} from '../../../providers/subject.service';
import {KindLessonsService} from '../../../providers/kind-lessons.service';

@Component({
  selector: 'app-studing-plan-edit',
  templateUrl: './studing-plan-edit.component.html',
  styleUrls: ['./studing-plan-edit.component.scss']
})
export class StudingPlanEditComponent implements OnInit {
  public controlData;
  public groupData;
  public periodData;
  public typeOccupationData;

  public data;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    grupa: '',
    univerDate: '',
    occupation: '',
    typeOccupation: '',
    hours: '',
    controllForm: ''
  };
  public curentData = {
    id: '',
    grupa: '',
    univerDate: '',
    typeOccupation: '',
    occupation: '',
    hours: '',
    controllForm: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: StudingPlanService,
              private groupProvider: GroupsService,
              private periodProvider: UniverDateService,
              private typeOccupationProvider: KindLessonsService,

              private controlProvider: ControlFormService,

              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    group: [],
    univerDate: [],
    occupation: [],
    hours: [],
    typeOccupation: [],
    controllForm: []
  });
  ngOnInit() {
    this.getOne(this.idS);
    this.getControl();
    this.getPeriods();
    this.getTypeOccupation();
    this.getGroups();

  }
  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectPeriod(data) {
    this.univerDate.setValue(data.id);
  }
  userSelectGroup(data) {
    this.group.setValue(data.name);
  }
  userSelectTypeOccupation(data) {
    this.typeOccupation.setValue(data.name);
  }
  getGroups() {
    this.groupProvider.getAllData()
      .subscribe(
        res => {
          this.groupData = res;
        },
        err => console.log(err)
      );
  }
  getControl() {
    this.controlProvider.getAllData()
      .subscribe(
        res => {
          this.controlData = res;
        },
        err => console.log(err)
      );
  }
  getTypeOccupation() {
    this.typeOccupationProvider.getAllData()
      .subscribe(
        res => {
          this.typeOccupationData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.group.setValue(this.curentData.grupa);
    this.typeOccupation.setValue(this.curentData.typeOccupation);
    this.univerDate.setValue(this.curentData.univerDate);
    this.occupation.setValue(this.curentData.occupation);
    this.hours.setValue(this.curentData.hours);
    this.controllForm.setValue(this.curentData.controllForm);
  }
  userSelectControl(data) {
    this.controllForm.setValue(data.id);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get typeOccupation() {
    return this.editForm.get('typeOccupation');
  }
  set typeOccupation(typeOccupation) {
    this.editForm.controls['typeOccupation'].setValue(typeOccupation);
  }
  get group() {
    return this.editForm.get('group');
  }
  set group(group) {
    this.editForm.controls['group'].setValue(group);
  }
  get univerDate() {
    return this.editForm.get('univerDate');
  }
  set univerDate(univerDate) {
    this.editForm.controls['univerDate'].setValue(univerDate);
  }
  get occupation() {
    return this.editForm.get('occupation');
  }
  set occupation(occupation) {
    this.editForm.controls['occupation'].setValue(occupation);
  }
  get hours() {
    return this.editForm.get('hours');
  }
  set hours(hours) {
    this.editForm.controls['hours'].setValue(hours);
  }
  get controllForm() {
    return this.editForm.get('controllForm');
  }
  set controllForm(controllForm) {
    this.editForm.controls['controllForm'].setValue(controllForm);
  }
  sendNewData() {
    this.newData.grupa = this.group.value;
    this.newData.id = this.id.value;
    this.newData.univerDate = this.univerDate.value;
    this.newData.hours = this.hours.value;
    this.newData.typeOccupation = this.typeOccupation.value;
    this.newData.occupation = this.occupation.value;
    this.newData.controllForm = this.controllForm.value;
    console.log(this.newData)
    this.provider.editData(this.newData);
  }
}
