import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {StudingPlanService} from '../../../providers/studing-plan.service';
import {ControlFormService} from '../../../providers/control-form.service';
import {GroupsService} from '../../../providers/groups.service';
import {UniverDateService} from '../../../providers/univer-date.service';
import {KindLessonsService} from '../../../providers/kind-lessons.service';

@Component({
  selector: 'app-studing-plan-create',
  templateUrl: './studing-plan-create.component.html',
  styleUrls: ['./studing-plan-create.component.scss']
})
export class StudingPlanCreateComponent implements OnInit {
  public controlData;
  public groupData;
  public typeOccupationData;
  public periodData;

  public newData = {
    id: '',
    grupa: '',
    univerDate: '',
    occupation: '',
    hours: '',
    typeOccupation: '',

    controllForm: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: StudingPlanService,
              private controlProvider: ControlFormService,
              private periodProvider: UniverDateService,
              private typeOccupationProvider: KindLessonsService,

              private groupProvider: GroupsService,

              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    group: [''],
    univerDate: [''],
    occupation: [''],
    hours: [''],
    typeOccupation: [],
    controllForm: ['']
  });
  ngOnInit() {
    this.getControl();
    this.getGroups();
    this.getPeriods();
    this.getTypeOccupation();

  }

  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectTypeOccupation(data) {
    this.typeOccupation.setValue(data.id);
  }
  get typeOccupation() {
    return this.newForm.get('typeOccupation');
  }
  set typeOccupation(typeOccupation) {
    this.newForm.controls['typeOccupation'].setValue(typeOccupation);
  }
  userSelectPeriod(data) {
    this.univerDate.setValue(data.id);
  }
  getControl() {
    this.controlProvider.getAllData()
      .subscribe(
        res => {
          this.controlData = res;
        },
        err => console.log(err)
      );
  }
  getGroups() {
    this.groupProvider.getAllData()
      .subscribe(
        res => {
          this.groupData = res;
        },
        err => console.log(err)
      );
  }
  userSelectControl(data) {
    this.controllForm.setValue(data.id);
  }
  userSelectGroup(data) {
    this.group.setValue(data.name);
  }
  get id() {
    return this.newForm.get('id');
  }
  set id(id) {
    this.newForm.controls['id'].setValue(id);
  }
  get group() {
    return this.newForm.get('group');
  }
  set group(group) {
    this.newForm.controls['group'].setValue(group);
  }
  get univerDate() {
    return this.newForm.get('univerDate');
  }
  set univerDate(univerDate) {
    this.newForm.controls['univerDate'].setValue(univerDate);
  }
  get occupation() {
    return this.newForm.get('occupation');
  }
  set occupation(occupation) {
    this.newForm.controls['occupation'].setValue(occupation);
  }
  get hours() {
    return this.newForm.get('hours');
  }
  set hours(hours) {
    this.newForm.controls['hours'].setValue(hours);
  }
  get controllForm() {
    return this.newForm.get('controllForm');
  }
  set controllForm(controllForm) {
    this.newForm.controls['controllForm'].setValue(controllForm);
  }
  getTypeOccupation() {
    this.typeOccupationProvider.getAllData()
      .subscribe(
        res => {
          this.typeOccupationData = res;
        },
        err => console.log(err)
      );
  }
  sendData() {
    this.newData.grupa = this.group.value;
    this.newData.id = this.id.value;
    this.newData.univerDate = this.univerDate.value;
    this.newData.hours = this.hours.value;
    this.newData.typeOccupation = this.typeOccupation.value;
    this.newData.occupation = this.occupation.value;
    this.newData.controllForm = this.controllForm.value;
    this.provider.newData(this.newData);
  }

}
