import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {StudentService} from '../providers/student.service';
import {CandidateWorkService} from '../providers/candidate-work.service';

@Component({
  selector: 'app-candidate-work',
  templateUrl: './candidate-work.component.html',
  styleUrls: ['./candidate-work.component.scss']
})
export class CandidateWorkComponent implements OnInit {
  public works = [];
  public updateDate;
  constructor(private provider: CandidateWorkService, private _router: Router) { }
  public routes = this.provider.routes;

  ngOnInit() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getWorks();
  }
  updateInfo() {
    this.updateDate = new Date().getHours() + ':' + new Date().getMinutes() + ':' + new Date().getSeconds();
    this.getWorks();
  }
  getWorks() {
    this.provider.getAllCandidateWork()
      .subscribe(
        res => {
          this.works = res;
        },
        err => console.log(err)
      );
  }
  delete(work) {
    this.provider.deleteCandidateWork(work.id);
  }
  goToEdit(work) {
    this._router.navigate([this.routes.edit + '/', work.id]);
  }
  goToNew() {
    this._router.navigate([this.routes.new + '/' , this.getIdForNewData() + 1]);
  }
  getIdForNewData() {
    let arr = [];
    for (let key = 0; key < this.works.length; key++) {
      arr[key] = this.works[key].id;
    }
    let max = Math.max(...arr);
    console.log(max);
    if (arr.length == 0) {
      return 0;
    } else {
      return max;
    }
  }

}
