import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {FacultyService} from '../../../providers/faculty.service';
import {TeacherService} from '../../../providers/teacher.service';

@Component({
  selector: 'app-faculty-edit',
  templateUrl: './faculty-edit.component.html',
  styleUrls: ['./faculty-edit.component.scss']
})
export class FacultyEditComponent implements OnInit {
  public teacherData;
  public data;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    name: '',
    dekanat: '',
    kerivnyk: ''
  };
  public curentData = {
    id: '',
    name: '',
    dekanat: '',
    kerivnyk: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private providerTeacher: TeacherService,
              private provider: FacultyService,
              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    name: [],
    kerivnyk: [],
    dekanat: [],
  });
  ngOnInit() {
    this.getTeachers();
    this.getOne(this.idS);
  }
  userSelectTeacher(data) {
    this.kerivnyk.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.name.setValue(this.curentData.name);
    this.dekanat.setValue(this.curentData.dekanat);
    this.kerivnyk.setValue(this.curentData.kerivnyk);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get name() {
    return this.editForm.get('name');
  }
  set name(name) {
    this.editForm.controls['name'].setValue(name);
  }
  get kerivnyk() {
    return this.editForm.get('kerivnyk');
  }
  set kerivnyk(kerivnyk) {
    this.editForm.controls['kerivnyk'].setValue(kerivnyk);
  }
  get dekanat() {
    return this.editForm.get('dekanat');
  }
  set dekanat(dekanat) {
    this.editForm.controls['dekanat'].setValue(dekanat);
  }
  sendNewData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.newData.dekanat = this.dekanat.value;
    this.newData.kerivnyk = this.kerivnyk.value;
    this.provider.editData(this.newData);
  }

}
