import { Component, OnInit } from '@angular/core';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {SubjectService} from '../../../providers/subject.service';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {GroupsService} from '../../../providers/groups.service';
import {KindLessonsService} from '../../../providers/kind-lessons.service';

@Component({
  selector: 'app-query8',
  templateUrl: './query8.component.html',
  styleUrls: ['./query8.component.scss']
})
export class Query8Component implements OnInit {


  public subjectData;

  public grupaData;

  public requestData = {
    subject: '',
    mark: '',
    grupa: ''
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private queryProvider: QueryProviderService,
              private subjectProvider: SubjectService,
              private grupaProvider: GroupsService,
              private typeOccupationProvider: KindLessonsService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    subject: [''],
    grupaName: [''],
    mark: ['']
  });
  ngOnInit() {
    this.getGrups();
    this.getSubjects();
  }
  userSelectSubject(data) {
    this.subject.setValue(data.id);
  }
  getGrups() {
    this.grupaProvider.getAllData()
      .subscribe(
        res => {
          this.grupaData = res;
        },
        err => console.log(err)
      );
  }
  getSubjects() {
    this.subjectProvider.getAllData()
      .subscribe(
        res => {
          this.subjectData = res;
        },
        err => console.log(err)
      );
  }
  userSelectGrupa(data) {
    this.grupaName.setValue(data.name);
  }
  set subject(subject) {
    this.requestForm.controls['subject'].setValue(subject);
  }
  get subject() {
    return this.requestForm.get('subject');
  }
  set mark(mark) {
    this.requestForm.controls['mark'].setValue(mark);
  }
  get mark() {
    return this.requestForm.get('mark');
  }
  set grupaName(grupaName) {
    this.requestForm.controls['grupaName'].setValue(grupaName);
  }
  get grupaName() {
    return this.requestForm.get('grupaName');
  }
  sendRequest() {
    this.requestData.grupa = this.grupaName.value;
    this.requestData.subject = this.subject.value;
    this.requestData.mark = this.mark.value;
    this.queryProvider.makeQuery8(this.requestData);
  }
}
