import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {OccupationService} from '../../../providers/occupation.service';
import {SubjectService} from '../../../providers/subject.service';

@Component({
  selector: 'app-subject-edit',
  templateUrl: './subject-edit.component.html',
  styleUrls: ['./subject-edit.component.scss']
})
export class SubjectEditComponent implements OnInit {

  public data;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    name: '',
  };
  public curentData = {
    id: '',
    name: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: SubjectService,
              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    name: [],
  });
  ngOnInit() {
    this.getOne(this.idS);
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.name.setValue(this.curentData.name);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get name() {
    return this.editForm.get('name');
  }
  set name(name) {
    this.editForm.controls['name'].setValue(name);
  }
  sendNewData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.provider.editData(this.newData);
  }

}
