import { Injectable } from '@angular/core';
import {environment} from '../../environments/environment';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
import {ApiService} from './api.service';

@Injectable({
  providedIn: 'root'
})
export class CandidateWorkService {
  public serverSrc = environment.serverSrc;
  public routes = {
    'main': '/candidate-work',
    'new': '/candidate-work/new',
    'edit': '/candidate-work/edit'
  };
  public maping = this._maping.candidate_work;
  constructor(private http: HttpClient, private _router: Router, private _maping: ApiService) { }
  getAllCandidateWork() {
    return this.http.get<any>(this.serverSrc + this.maping['showAll']);
  }
  getOneCandidateWork(id) {
    return this.http.get<any>(this.serverSrc + this.maping['showOne'] + '/' + id);
  }
  editCandidateWork(work) {
    this.http.post(this.serverSrc + this.maping['edit'], work)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  redirectToMainPage() {
    this._router.navigate([this.routes['main']]);
  }
  newCandidateWork(work) {
    return this.http.post(this.serverSrc + this.maping['create'], work)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  deleteCandidateWork(id) {
    return this.http.post(this.serverSrc + this.maping['delete'], id)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
}
