import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {GroupsService} from '../../../providers/groups.service';
import {DiplomaService} from '../../../providers/diploma.service';
import {TeacherService} from '../../../providers/teacher.service';
import {StudentService} from '../../../providers/student.service';

@Component({
  selector: 'app-diploma-create',
  templateUrl: './diploma-create.component.html',
  styleUrls: ['./diploma-create.component.scss']
})
export class DiplomaCreateComponent implements OnInit {
  public teacherData;
  public studentData;

  public newData = {
    id: '',
    student: '',
    kerivnyk: '',
    theme: '',
    date: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: DiplomaService,
              private studentProvider: StudentService,
              private providerTeacher: TeacherService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    student: [''],
    kerivnyk: [''],
    theme: [''],
    date: ['']
  });
  ngOnInit() {
    this.getTeachers();
    this.getStudents();

  }
  userSelectTeacher(data) {
    this.kerivnyk.setValue(data.id);
  }
  userSelectStudent(data) {
    this.student.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getStudents() {
    this.studentProvider.getAllStudents()
      .subscribe(
        res => {
          this.studentData = res;
        },
        err => console.log(err)
      );
  }
  get id() {
    return this.newForm.get('id');
  }
  get student() {
    return this.newForm.get('student');
  }
  get kerivnyk() {
    return this.newForm.get('kerivnyk');
  }
  get theme() {
    return this.newForm.get('theme');
  }
  get date() {
    return this.newForm.get('date');
  }
  sendData() {
    this.newData.student = this.student.value;
    this.newData.id = this.id.value;
    this.newData.kerivnyk = this.kerivnyk.value;
    this.newData.theme = this.theme.value;
    this.newData.date = this.date.value;
    this.provider.newData(this.newData);
  }

}
