import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {ScientificThemeService} from '../../../providers/scientific-theme.service';

@Component({
  selector: 'app-scientific-theme-create',
  templateUrl: './scientific-theme-create.component.html',
  styleUrls: ['./scientific-theme-create.component.scss']
})
export class ScientificThemeCreateComponent implements OnInit {

  public newData = {
    id: '',
    themeOfWork: '',
    teacher: '',
    date: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: ScientificThemeService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    date: [''],
    teacher: [''],
    themeOfWork: [''],
  });
  ngOnInit() {
  }
  get id() {
    return this.newForm.get('id');
  }
  get themeOfWork() {
    return this.newForm.get('themeOfWork');
  }
  get teacher() {
    return this.newForm.get('teacher');
  }
  get date() {
    return this.newForm.get('date');
  }
  sendData() {
    this.newData.date = this.date.value;
    this.newData.id = this.id.value;
    this.newData.teacher = this.teacher.value;
    this.newData.themeOfWork = this.themeOfWork.value;
    this.provider.newData(this.newData);
  }

}
