import { Injectable } from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {environment} from '../../environments/environment';
import {Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class StudentService {
  private serverSrc: string;
  constructor(private http: HttpClient, private _router: Router) {
    this.serverSrc = environment.serverSrc;
  }
  getAllStudents() {
    return this.http.get<any>(this.serverSrc + '/api/student/list');
  }
  getOneStudent(id) {
    return this.http.get<any>(this.serverSrc + '/api/student/getOne/' + id);
  }
  editStudent(student) {
    this.http.post(this.serverSrc + '/api/student/update', student)
      .subscribe(res => {
        this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  redirectToMainPage() {
    this._router.navigate(['/students']);
  }

  newStudent(student) {
    return this.http.post(this.serverSrc + '/api/student/create', student)
      .subscribe(res => {
        this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  deleteStudent(id) {
    return this.http.post(this.serverSrc + '/api/student/delete', id)
      .subscribe(res => {
          this.redirectToMainPage();
        }, (err) => {
          console.log(err);
        }
      );
  }
  makeRequest1(student) {
    return this.http.post(this.serverSrc + '/api/student/findByParams', student)
      .subscribe(res => {
        this._router.navigate(['/query/result', '1']);
        }, (err) => {
          console.log(err);
        }
      );
  }
  getSearchStudents() {
    return this.http.get<any>(this.serverSrc + '/api/student/findByParams');
  }
}
