import { Component, OnInit } from '@angular/core';
import {FormBuilder} from '@angular/forms';
import {CandidateWorkService} from '../../providers/candidate-work.service';
import {ActivatedRoute, Router} from '@angular/router';
import {GroupsService} from '../../providers/groups.service';
import {TeacherService} from '../../providers/teacher.service';
import {KafedraService} from '../../providers/kafedra.service';

@Component({
  selector: 'app-groups-create',
  templateUrl: './groups-create.component.html',
  styleUrls: ['./groups-create.component.scss']
})
export class GroupsCreateComponent implements OnInit {
  public teacherData;
  public kafedraData;
  public newData = {
    id: '',
    name: '',
    kurator: '',
    kafedra: '',
    year: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: GroupsService,
              private _actRouter: ActivatedRoute,
              private providerKafedra: KafedraService,
              private providerTeacher: TeacherService
    ) { }
  newForm = this.fb.group({
    id: [this.idS],
    name: [''],
    kurator: [''],
    year: [''],
    kafedra: ['']
  });
  ngOnInit() {
  this.getTeachers();
    this.getKafedra();

  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getKafedra() {
    this.providerKafedra.getAllData()
      .subscribe(
        res => {
          this.kafedraData = res;
        },
        err => console.log(err)
      );
  }
  userSelectKafedra(data) {
    this.kafedra.setValue(data.name);

  }
  get id() {
    return this.newForm.get('id');
  }
  get kurator() {
    return this.newForm.get('kurator');
  }
  get name() {
    return this.newForm.get('name');
  }
  get year() {
    return this.newForm.get('year');
  }
  get kafedra() {
    return this.newForm.get('kafedra');
  }
  sendData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.newData.kurator = this.kurator.value;
    this.newData.year = this.year.value;
    this.newData.kafedra = this.kafedra.value;
    console.log(this.newData)
    this.provider.newData(this.newData);
  }
  userSelectTeacher(data) {
    this.kurator.setValue(data.id);
  }
}
