import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {SessionService} from '../../../providers/session.service';
import {StudentService} from '../../../providers/student.service';
import {TeacherService} from '../../../providers/teacher.service';
import {SubjectService} from '../../../providers/subject.service';
import {ControlFormService} from '../../../providers/control-form.service';
import {UniverDateService} from '../../../providers/univer-date.service';

@Component({
  selector: 'app-session-create',
  templateUrl: './session-create.component.html',
  styleUrls: ['./session-create.component.scss']
})
export class SessionCreateComponent implements OnInit {
  public teacherData;
  public controlData;
  public studentData;
  public subjectData;
  public periodData;


  public newData = {
    id: '',
    student: '',
    subject: '',
    teacher: '',
    mark: '',
    controllForm: '',
    univerDate: ''
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private studentProvider: StudentService,
              private providerTeacher: TeacherService,
              private controlProvider: ControlFormService,
              private periodProvider: UniverDateService,

              private subjectProvider: SubjectService,

              private provider: SessionService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    student: [''],
    subject: [''],
    teacher: [''],
    mark: [''],
    controllForm: [''],
    univerDate: ['']
  });
  ngOnInit() {
    this.getTeachers();
    this.getStudents();
    this.getSubject();
    this.getControl();
    this.getPeriods();

  }
  getPeriods() {
    this.periodProvider.getAllData()
      .subscribe(
        res => {
          this.periodData = res;
        },
        err => console.log(err)
      );
  }
  userSelectPeriod(data) {
    this.univerDate.setValue(data.id);
  }
  getStudents() {
    this.studentProvider.getAllStudents()
      .subscribe(
        res => {
          this.studentData = res;
        },
        err => console.log(err)
      );
  }
  getSubject() {
    this.subjectProvider.getAllData()
      .subscribe(
        res => {
          this.subjectData = res;
        },
        err => console.log(err)
      );
  }
  getControl() {
    this.controlProvider.getAllData()
      .subscribe(
        res => {
          this.controlData = res;
        },
        err => console.log(err)
      );
  }
  userSelectSubject(data) {
    this.subject.setValue(data.id);
  }
  userSelectControl(data) {
    this.controllForm.setValue(data.id);
  }
  userSelectStudent(data) {
    this.student.setValue(data.id);
  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  get id() {
    return this.newForm.get('id');
  }
  set id(id) {
    this.newForm.controls['id'].setValue(id);
  }
  get student() {
    return this.newForm.get('student');
  }
  set student(student) {
    this.newForm.controls['student'].setValue(student);
  }
  get subject() {
    return this.newForm.get('subject');
  }
  set subject(subject) {
    this.newForm.controls['subject'].setValue(subject);
  }
  get teacher() {
    return this.newForm.get('teacher');
  }
  set teacher(teacher) {
    this.newForm.controls['teacher'].setValue(teacher);
  }
  get mark() {
    return this.newForm.get('mark');
  }
  set mark(mark) {
    this.newForm.controls['mark'].setValue(mark);
  }

  get controllForm() {
    return this.newForm.get('controllForm');
  }
  set controllForm(controllForm) {
    this.newForm.controls['controllForm'].setValue(controllForm);
  }
  get univerDate() {
    return this.newForm.get('univerDate');
  }
  set univerDate(univerDate) {
    this.newForm.controls['univerDate'].setValue(univerDate);
  }
  sendData() {
    this.newData.univerDate = this.univerDate.value;
    this.newData.student = this.student.value;
    this.newData.controllForm = this.controllForm.value;
    this.newData.mark = this.mark.value;
    this.newData.teacher = this.teacher.value;
    this.newData.id = this.id.value;
    this.newData.subject = this.subject.value;
    console.log(this.newData)
    this.provider.newData(this.newData);
  }

}
