import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {UniverDateService} from '../../../providers/univer-date.service';

@Component({
  selector: 'app-univer-date-edit',
  templateUrl: './univer-date-edit.component.html',
  styleUrls: ['./univer-date-edit.component.scss']
})
export class UniverDateEditComponent implements OnInit {

  public data;
  public updateDate;
  public routes = this.provider.routes;
  public newData = {
    id: '',
    year: '',
    semester: ''
  };
  public curentData = {
    id: '',
    year: '',
    semester: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: UniverDateService,
              private _actRouter: ActivatedRoute) { }

  editForm = this.fb.group({
    id: [this.curentData.id],
    year: [],
    semester: [],
  });
  ngOnInit() {
    this.getOne(this.idS);
  }
  getOne(idS) {
    this.provider.getOneData(idS)
      .subscribe(
        res => {
          this.curentData = res;
          this.whenDataCome();
        },
        err => console.log(err)
      );
  }
  whenDataCome() {
    this.id.setValue(this.curentData.id);
    this.year.setValue(this.curentData.year);
    this.semester.setValue(this.curentData.semester);
  }
  get id() {
    return this.editForm.get('id');
  }
  set id(id) {
    this.editForm.controls['id'].setValue(id);
  }
  get semester() {
    return this.editForm.get('semester');
  }
  set semester(semester) {
    this.editForm.controls['semester'].setValue(semester);
  }
  get year() {
    return this.editForm.get('year');
  }
  set year(year) {
    this.editForm.controls['year'].setValue(year);
  }
  sendNewData() {
    this.newData.year = this.year.value;
    this.newData.id = this.id.value;
    this.newData.semester = this.semester.value;
    this.provider.editData(this.newData);
  }

}
