import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {UniverDateService} from '../../../providers/univer-date.service';

@Component({
  selector: 'app-univer-date-create',
  templateUrl: './univer-date-create.component.html',
  styleUrls: ['./univer-date-create.component.scss']
})
export class UniverDateCreateComponent implements OnInit {

  public newData = {
    id: '',
    year: '',
    semester: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: UniverDateService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    year: [''],
    semester: [''],
  });
  ngOnInit() {
  }
  get id() {
    return this.newForm.get('id');
  }
  get year() {
    return this.newForm.get('year');
  }
  get semester() {
    return this.newForm.get('semester');
  }
  sendData() {
    this.newData.semester = this.semester.value;
    this.newData.id = this.id.value;
    this.newData.year = this.year.value;
    this.provider.newData(this.newData);
  }
}
