import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {FacultyService} from '../../../providers/faculty.service';
import {TeacherService} from '../../../providers/teacher.service';

@Component({
  selector: 'app-faculty-create',
  templateUrl: './faculty-create.component.html',
  styleUrls: ['./faculty-create.component.scss']
})
export class FacultyCreateComponent implements OnInit {
  public teacherData;

  public newData = {
    id: '',
    name: '',
    dekanat: '',
    kerivnyk: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private providerTeacher: TeacherService,

              private provider: FacultyService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    name: [''],
    kerivnyk: [''],
    dekanat: [''],
  });
  ngOnInit() {
    this.getTeachers();

  }
  userSelectTeacher(data) {
    this.kerivnyk.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  get id() {
    return this.newForm.get('id');
  }
  get kerivnyk() {
    return this.newForm.get('kerivnyk');
  }
  get name() {
    return this.newForm.get('name');
  }
  get dekanat() {
    return this.newForm.get('dekanat');
  }
  set kerivnyk(kerivnyk) {
    this.newForm.controls['kerivnyk'].setValue(kerivnyk);
  }
  sendData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.newData.dekanat = this.dekanat.value;
    this.newData.kerivnyk = this.kerivnyk.value;
    this.provider.newData(this.newData);
  }


}
