import { Component, OnInit } from '@angular/core';
import {QueryProviderService} from '../../../providers/query-provider.service';
import {SubjectService} from '../../../providers/subject.service';
import {FormBuilder} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {GroupsService} from '../../../providers/groups.service';
import {KindLessonsService} from '../../../providers/kind-lessons.service';

@Component({
  selector: 'app-query6',
  templateUrl: './query6.component.html',
  styleUrls: ['./query6.component.scss']
})
export class Query6Component implements OnInit {


  public grupaData;
  public subjectData;
  public typeOccupationData;

  public requestData = {
    subjectId: '',
    grupaName: ''
  };
  constructor(private _router: Router,
              private fb: FormBuilder,
              private queryProvider: QueryProviderService,
              private subjectProvider: SubjectService,
              private grupaProvider: GroupsService,
              private typeOccupationProvider: KindLessonsService,
              private _actRouter: ActivatedRoute) { }
  requestForm = this.fb.group({
    subjectId: [''],
    grupaName: ['']
  });
  ngOnInit() {
    this.getGrups();
    this.getTypeOccupation();
    this.getSubjects();
  }
  userSelectTypeOccupation(data) {
    this.subjectId.setValue(data.name);
  }
  getTypeOccupation() {
    this.typeOccupationProvider.getAllData()
      .subscribe(
        res => {
          this.typeOccupationData = res;
        },
        err => console.log(err)
      );
  }
  getGrups() {
    this.grupaProvider.getAllData()
      .subscribe(
        res => {
          this.grupaData = res;
        },
        err => console.log(err)
      );
  }
  getSubjects() {
    this.subjectProvider.getAllData()
      .subscribe(
        res => {
          this.subjectData = res;
        },
        err => console.log(err)
      );
  }
  userSelectGrupa(data) {
    this.grupaName.setValue(data.name);
  }
  set subjectId(subjectId) {
    this.requestForm.controls['subjectId'].setValue(subjectId);
  }
  get subjectId() {
    return this.requestForm.get('subjectId');
  }
  set grupaName(grupaName) {
    this.requestForm.controls['grupaName'].setValue(grupaName);
  }
  get grupaName() {
    return this.requestForm.get('grupaName');
  }
  sendRequest() {
    this.requestData.grupaName = this.grupaName.value;
    this.requestData.subjectId = this.subjectId.value;
    console.log(this.requestData)
    this.queryProvider.makeQuery6(this.requestData);
  }


}
