import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {DecanatService} from '../../../providers/decanat.service';
import {KafedraService} from '../../../providers/kafedra.service';
import {TeacherService} from '../../../providers/teacher.service';
import {FacultyService} from '../../../providers/faculty.service';

@Component({
  selector: 'app-kafedra-create',
  templateUrl: './kafedra-create.component.html',
  styleUrls: ['./kafedra-create.component.scss']
})
export class KafedraCreateComponent implements OnInit {
  public teacherData;
  public facultyData;

  public newData = {
    id: '',
    name: '',
    zavKafedra: '',
    faculty: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: KafedraService,
              private providerTeacher: TeacherService,
              private providerFaculty: FacultyService,

              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    name: [''],
    faculty: [''],
    zavKafedra: [''],
  });
  ngOnInit() {
    this.getTeachers();
    this.getFaculty();

  }
  set zavKafedra(zavKafedra) {
    this.newForm.controls['zavKafedra'].setValue(zavKafedra);
  }
  set faculty(faculty) {
    this.newForm.controls['faculty'].setValue(faculty);
  }
  get id() {
    return this.newForm.get('id');
  }
  userSelectTeacher(data) {
    this.zavKafedra.setValue(data.id);
  }
  userSelectFaculty(data) {
    this.faculty.setValue(data.id);
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  getFaculty() {
    this.providerFaculty.getAllData()
      .subscribe(
        res => {
          this.facultyData = res;
        },
        err => console.log(err)
      );
  }
  get zavKafedra() {
    return this.newForm.get('zavKafedra');
  }
  get name() {
    return this.newForm.get('name');
  }
  get faculty() {
    return this.newForm.get('faculty');
  }
  sendData() {
    this.newData.name = this.name.value;
    this.newData.id = this.id.value;
    this.newData.faculty = this.faculty.value;
    this.newData.zavKafedra = this.zavKafedra.value;
    this.provider.newData(this.newData);
  }


}
