import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder} from '@angular/forms';
import {StudentService} from '../../providers/student.service';
import {CandidateWorkService} from '../../providers/candidate-work.service';
import {TeacherService} from '../../providers/teacher.service';

@Component({
  selector: 'app-candidate-work-new',
  templateUrl: './candidate-work-new.component.html',
  styleUrls: ['./candidate-work-new.component.scss']
})
export class CandidateWorkNewComponent implements OnInit {
  public newWorkData = {
    id: '',
    themeOfWork: '',
    teacher: '',
    date: '',
  };
  public idS = this._actRouter.snapshot.paramMap.get('id');

  public teacherData;
  public routes = this.provider.routes;
  constructor(private _router: Router,
              private fb: FormBuilder,
              private provider: CandidateWorkService,
              private providerTeacher: TeacherService,
              private _actRouter: ActivatedRoute) { }
  newForm = this.fb.group({
    id: [this.idS],
    themeOfWork: [''],
    teacher: [''],
    date: ['']
  });
  ngOnInit() {
    this.getTeachers();
  }
  getTeachers() {
    this.providerTeacher.getAllData()
      .subscribe(
        res => {
          this.teacherData = res;
        },
        err => console.log(err)
      );
  }
  get id() {
    return this.newForm.get('id');
  }
  get teacher() {
    return this.newForm.get('teacher');
  }
  get date() {
    return this.newForm.get('date');
  }
  get themeOfWork() {
    return this.newForm.get('themeOfWork');
  }
  set teacher(teacher) {
    this.newForm.controls['teacher'].setValue(teacher);
  }
  sendData() {
    this.newWorkData.teacher = this.teacher.value;
    this.newWorkData.id = this.id.value;
    this.newWorkData.date = this.date.value;
    this.newWorkData.themeOfWork = this.themeOfWork.value;
    console.log(this.newWorkData)
    this.provider.newCandidateWork(this.newWorkData);
  }
  userSelectTeacher(data) {
    this.teacher.setValue(data.id);
  }
}
